import Vue from "vue";
import VueRouter from "vue-router";
import Index from "../views/index/Index.vue";
import Error from "../views/404.vue";
import Comic from "../views/Comic.vue";
import Tag from "../views/Tag.vue";
import Reader from "../views/Reader.vue";
import Sign from "../views/sign/index.vue";
import Login from "../views/sign/login.vue";
import SignUp from "../views/sign/signup.vue";
import Rank from "../views/Rank.vue";
import Homepage from "../views/homepage/Homepage.vue";
import info from "../views/homepage/info.vue";
import collect from "../views/homepage/collect.vue";
import history from "../views/homepage/history.vue";
import comment from "../views/homepage/comment.vue";
import like from "../views/homepage/like.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Index
  },
  {
    path: "/index",
    redirect: "/"
  },
  {
    path: "/comic/:id",
    component: Comic
  },
  {
    path: "/tag/:id",
    component: Tag
  },
  {
    path: "/reader/:id",
    component: Reader
  },
  {
    path: "/sign",
    component: Sign,
    children: [
      {
        path: "",
        redirect: "login"
      },
      {
        path: "login",
        component: Login
      },
      {
        path: "signup",
        component: SignUp
      }
    ]
  },
  {
    path: "/homepage",
    component: Homepage,
    children: [
      {
        path: "",
        redirect: "info"
      },
      {
        path: "info",
        component: info
      },
      {
        path: "collect",
        component: collect
      },
      {
        path: "record",
        component: history
      },
      {
        path: "comment",
        component: comment
      },
      {
        path: "like",
        component: like
      }
    ]
  },
  {
    path: "/rank",
    component: Rank
  },
  {
    path: "*",
    component: Error
  }
];

const router = new VueRouter({
  routes,
  mode: "history"
});

export default router;
