<template>
  <div class="reader">
    <v-header></v-header>
    <div class="container">
      <div class="crumb" v-if="comicInfo">
        <router-link tag="span" to="/">首页</router-link>
        <span> > </span>
        <router-link tag="span" :to="'/comic/' + this.comicInfo.id">{{
          this.comicInfo.title
        }}</router-link>
        <span> > </span>
        <span> {{ this.thisChapter }} </span>
      </div>
      <p>该资源爬取自互联网.</p>
      <ul class="imgList">
        <li class="imgItem" v-for="url in this.imgList" :key="url">
          <img :data-src="url" class="lazy-image" alt="" />
        </li>
      </ul>
    </div>

    <div :class="{ catelog: true, hide: showNav }">
      <button @click="changeNav">目录</button>
      <ul class="chapterList">
        <li
          v-for="item in chapterList"
          :key="item.chapter_id"
          @click="to(item.chapter_id)"
          :class="{ currentchap: item.chapter_id == $route.params.id }"
        >
          {{ item.title }}
        </li>
      </ul>
      <div class="total">共{{ chapterList.length }}话免费章节</div>
    </div>
    <button class="navBtn" @click="changeNav">
      <em>&lt;</em>
    </button>
    <div :class="{ hide: showNav }" class="nav">
      <ul class="wrap">
        <li @click="swicher('prev')">&lt;上一话</li>
        <li @click="swicher('next')">下一话&gt;</li>
        <li @click="addCollection">添加收藏</li>
        <li>我的收藏</li>
      </ul>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Header";
import { lazyLoad } from "@/utils";
import { chapterDetail, imgList, addRecord, userInfo } from "@/config/API";
export default {
  components: {
    "v-header": Header
  },
  data() {
    return {
      imgList: [],
      chapterList: [],
      comicInfo: null,
      showNav: true
    };
  },
  computed: {
    thisChapter() {
      const thisChapter = this.chapterList.find(
        ele => ele.chapter_id == this.$route.params.id
      );
      return thisChapter.title;
    }
  },
  async beforeRouteUpdate(to, from, next) {
    const list = await imgList(this.$route.params.id);
    this.imgList = list.imgList;
    console.log(this.imgList.length);
    next();
  },
  watch: {},
  methods: {
    //更换章节
    async to(cId) {
      this.$router.push({
        path: "/reader/" + cId,
        query: { comicId: this.$route.query.comicId }
      });
      // const list = await imgList(this.$route.params.id);
      // this.imgList = list.data.imgList;
    },
    changeNav() {
      document.querySelector(".chapterList").scrollTop =
        this.chapterList.indexOf(this.$route.params.id) * 40;
      this.showNav = !this.showNav;
    },
    swicher(keyword) {
      const index = this.chapterList.findIndex(
        ele => ele.chapter_id == this.$route.params.id
      );
      if (keyword == "prev") {
        console.log(index);
      } else if (keyword == "next") {
        console.log(index);
      }
    },
    addCollection() {
      return;
    }
  },
  async created() {
    const info = await chapterDetail(
      this.$route.query.comicId,
      this.$route.params.id
    );
    console.log(info);
    this.comicInfo = info.comicInfo;
    this.chapterList = info.chapterList;
    const list = await imgList(this.$route.params.id);
    this.imgList = list.imgList;
    console.log(this.comicInfo);
    await addRecord(
      this.comicInfo.id,
      this.$route.params.id,
      this.comicInfo.title,
      this.thisChapter
    );
    userInfo().then(res => {
      this._utils.setLocalStorage("userInfo", JSON.stringify(res.data));
    });
  },
  updated() {
    lazyLoad(".lazy-image");
  }
};
</script>
<style lang="less" scoped>
.reader {
  .container {
    width: 1068px;
    margin: 0 auto;
    font-size: 18px;
    .crumb {
      padding: 24px 0;
      text-align: center;
    }
    p {
      margin-bottom: 24px;
      text-align: center;
    }
    .imgList {
      width: 100%;
      text-align: center;
      .imgItem {
        .lazy-image {
          display: inline-block;
          width: 564px;
          height: 300px;
          background: url("../assets/loading.gif") no-repeat;
          background-size: cover;
        }
      }
    }
  }
  //目录
  .catelog {
    position: fixed;
    right: 0;
    top: 30%;
    width: 350px;
    background-color: #f1f3f2;
    // background-color: black;
    transition: 300ms all linear;
    .chapterList {
      max-height: 540px;
      overflow: scroll;
      padding: 10px;
      li {
        font-size: 16px;
        padding: 7px;
      }
      .currentchap {
        color: red;
      }
    }
    button {
      .btn(@width:30px, @height: 70px);
      position: absolute;
      left: -30px;
      top: 0;
    }
    .total {
      height: 40px;
      color: #fff;
      background-color: @btn-color;
      text-align: center;
      padding-top: 9px;
      font-size: 15px;
    }
  }
  //底部导航栏按钮
  .navBtn {
    z-index: 99;
    outline: none;
    border: 0;
    position: fixed;
    right: 0px;
    bottom: 10px;
    width: 35px;
    height: 35px;
    background-color: black;
    color: #fff;
    font-size: 25px;
    transition: 300ms all linear;
    &:hover {
      background-color: @hover-color;
    }
    &:active {
      background-color: @focus-color;
    }
    em {
      display: block;
      width: 100%;
      height: 100%;
      transform: translateY(-3px);
    }
  }
  .radio {
    border-radius: 23px 0 0 23px;
  }

  //底部导航栏

  .nav {
    position: fixed;
    right: 35px;
    bottom: 10px;
    color: #fff;
    height: 35px;
    font-size: 16px;
    transition: 300ms all linear;
    li {
      &:first-child {
        border-radius: 23px 0 0 23px;
        padding-left: 20px;
      }
      background-color: @btn-color;
      &:hover {
        background-color: @hover-color;
      }
      &:active {
        background-color: @focus-color;
      }
      vertical-align: middle;
      line-height: 35px;
      display: inline-block;
      padding: 0 10px;
    }
  }
  .hide {
    transform: translateX(100%);
  }
}
</style>
